footer {
    box-sizing: border-box;
    width: 100%;
    padding: 50px 0 25px 0;
    ul {
        display: flex;
        justify-content: center;
        column-gap: 20px;

        li {
            a {
               display: block;
               position: relative; 
                &::before {
                    content: "";
                    position: absolute;
                    width: 0%;
                    top: 100%;
                    left: 0;
                    height: 3px;
                    border-radius: 2px;
                    background: $gradientMain;
                    opacity: 0.2;
                    transition: all ease 0.25s;
                }

                &:hover {
                    &::before {
                        width: 100%;
                    }
                }
            }
        }
    }
}