.contactForm-container {
    @include grid-container;

    h2 {
        grid-column: 1 / 4;

        &::before {
            content: "";
            position: absolute;
            width: 0;
            bottom: -20%;
            left: 0;
            height: 20px;
            border-radius: 2px;
            background: $gradientMain;
            opacity: 0.2;
            transition: all ease 0.5s;

        }

        &.in-view {
            &::before {
                width: 65%;
            }
        }
    }

    .contactForm {
        grid-row: 2;
        display: flex;
        flex-direction: column;
        row-gap: 15px;
        font-size: 25px;
        padding-bottom: 50px;

        li {
            display: flex;
        }

        a {
            display: flex;
            align-items: center;
            column-gap: 10px;
            position: relative; 
             &::before {
                 content: "";
                 position: absolute;
                 width: 0%;
                 top: 100%;
                 left: 0;
                 height: 3px;
                 border-radius: 2px;
                 background: $gradientMain;
                 opacity: 0.2;
                 transition: all ease 0.25s;
             }

             &:hover {
                 &::before {
                     width: 100%;
                 }
             }
        }
    }
}