header {
    display: grid;
    grid-template-columns: repeat(8,1fr);
    column-gap: 20px;
    width: 90%;
    margin: 0 auto;
    height: 105vh;

    h1 {
        display: grid;
        grid-template-columns: repeat(5,1fr);
        grid-column: 2 / 6 ;
        column-gap: 20px;
        height: fit-content;
        margin: 30% 0 0 0;
        
        span {
            text-align: right;
        }

        span:first-child {
            grid-column: 1 / 6;
            font-size: 96px;
            line-height: 96px;
            -webkit-text-stroke: 1px black;
            color: transparent;
        }
        span:nth-child(2) {
            grid-column: 2 / 6;
            font-size: 52px;
            line-height: 52px;
        }
    }

    img {
        margin: 62% 0 0 0;
        width: 200px;
        grid-column: 6 / 8;
    }

    .CTA-text {
        font-style: italic;
        font-weight: bold;
        position: absolute;
        left: 50%;
        bottom: 110px;
        transform: translateX(-50%);
        border-left: 2px solid black;
        border-right: 2px solid black;
        padding: 0 10px;
        transition: all ease-out 0.5s;
    }

    .CTA-hidden {
        opacity: 0;
    }

