@mixin grid-container {
    display: grid;
    width: 80%;
    grid-template-columns: repeat(8,1fr);
    grid-template-rows: repeat(2, auto);
    column-gap: 20px;
    row-gap: 60px;
    margin: 0 auto;
}

@mixin hover-link {
    .hover-link {
        position: relative;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        color: white;
        opacity: 0;
        transition: 0.2s all ease;

        span {
            position: absolute;
            font-size: 52px;
            line-height: 52px;
            -webkit-text-stroke: 0.5px white;
            color: transparent;            
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }
    }

    &:hover {
        .hover-link {
            opacity: 1;
        }
    }
}