.whoAmI-container {
    display: grid;
    width: 80%;
    grid-template-columns: repeat(8,1fr);
    column-gap: 20px;
    row-gap: 60px;
    margin: 0 auto;
    padding-top: 100px;

    h2 {
        &::before {
            content: "";
            position: absolute;
            width: 0;
            bottom: -20%;
            left: 0;
            height: 20px;
            border-radius: 2px;
            background: $gradientMain;
            opacity: 0.2;
            transition: all ease 0.5s;
        }

        &.in-view {
            &::before {
                width: 40%;
            }
        }
    }

    div:first-child {
        display: flex;
        flex-direction: column;
        row-gap: 50px;
        grid-column: 1 / 5 ;

        .PhotoProfil-container {
            display: block;
            width: 270px;
            height: 270px;
            background-image: url('../../../public/images/PhotoProfil.jpg');
            background-repeat: no-repeat;
            background-position: center 20%;
            background-size: cover;
            box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.4) inset;
            -webkit-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.4) inset;
            -moz-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.4) inset;
            border-radius: 10px;
            margin: 0 auto;
        }
    }

    div:nth-child(2) {
        grid-column: 6 / 9 ;
    }
}