.CTA-scroll {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    bottom: 50px ;
    left: 50%;
    transform: translateX(-50%) rotate(0deg);
    border: 2px solid black;
    border-radius: 50%;
    transition: all ease-in 1s;
    cursor: pointer;

    &.CTA-left {
        left: 45px;
        transform: translateX(-50%) rotate(-360deg);
        background-color: black;
        cursor: default;

        svg {
            fill: white;
        }

    }
}