nav {
    position: fixed;
    z-index: 10;
    top: 0;
    box-sizing: border-box;
    width: 100%;
    padding: 20px 40px;
    background-color: rgba(white, 0.7) ;
    backdrop-filter: blur(0.5px);

    ul {
        display: flex;
        align-items: center;
        column-gap: 70px;

        li {
            a {
               display: block;
               position: relative; 
                &::before {
                    content: "";
                    position: absolute;
                    width: 0%;
                    top: 100%;
                    left: 0;
                    height: 3px;
                    border-radius: 2px;
                    background: $gradientMain;
                    opacity: 0.2;
                    transition: all ease 0.25s;
                }

                &:hover {
                    &::before {
                        width: 100%;
                    }
                }
            }
        }
    }
    
    img {
        width: 60px;
        height: auto;
    }
}