.projectOne-container {
    @include grid-container;

    h2 {
        grid-column: 1 / 6 ;

        &::before {
            content: "";
            position: absolute;
            width: 0;
            bottom: -20%;
            left: 0;
            height: 20px;
            border-radius: 2px;
            background: $gradientMain;
            opacity: 0.2;
            transition: all ease 0.5s;

        }

        &.in-view {
            &::before {
                width: 80%;
            }
        }
    }

    .img-container {
        grid-column: 1 / 5 ;
        overflow: hidden;
        box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.4) inset;
        -webkit-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.4) inset;
        -moz-box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.4) inset;
        border-radius: 10px;
    }

    .imgProjectOne {
        background: url('../../../public/images/campInstImg.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .gray-text-container {
        grid-column: 5 / 9 ;
    }
}

