.toolsProjects {
    display: flex;
    flex-wrap: wrap;
    row-gap: 15px;
    li {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: fit-content;
        flex: 0 0 33.333333%;
        row-gap: 5px;
    }
}

.projectTwo-container {
    @include grid-container;

    h2 {
