        &::before {
            content: "";
            position: absolute;
            width: 0;
            bottom: -20%;
            right: 0;
            height: 20px;
            border-radius: 2px;
            background: $gradientMain;
            opacity: 0.2;
            transition: all ease 0.5s;

        }

        &.in-view {
            &::before {
                width: 80%;
            }
        }
    }

    .gray-text-container {
        grid-column: 1 / 5 ;
    }

    .img-container {
